@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&family=Poppins:wght@700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 100%;
}

ul,
ol {
  list-style: none;
}

body {
  min-height: 100vh;
  font-family: "Open Sans", sans-serif;
  background-color: var(--secondary-white);
}

img {
  width: 100%;
}

:root {
  --primary-pink: hsl(322deg, 100%, 66%);
  --primary-light-pink: hsl(321deg, 100%, 78%);
  --primary-light-red: hsl(0deg, 100%, 63%);
  --secondary-very-dark-cyan: hsl(192deg, 100%, 9%);
  --secondary-very-pale-blue: hsl(207deg, 100%, 98%);
  --secondary-shadow-gray: rgba(0, 0, 0, 0.1);
  --secondary-light-gray: hsl(0deg, 0%, 56%);
  --secondary-white: hsl(0deg, 0%, 100%);
  --fs-100: 0.825rem;
  --fs-200: 1rem;
  --fs-300: 1.275rem;
  --fs-400: 1.625rem;
  --fs-500: 2.225rem;
  --fs-600: 2.827rem;
  --fs-700: 4rem;
  --fs-800: 5.6rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  color: var(--secondary-very-dark-blue);
}

h1,
.heading-1 {
  font-size: var(--fs-400);
  line-height: 1.4;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  h1,
.heading-1 {
    font-size: var(--fs-600);
  }
}

h2,
.heading-2 {
  font-size: var(--fs-300);
  margin-bottom: 0.8rem;
}
h2--number,
.heading-2--number {
  font-size: var(--fs-700);
}
@media (min-width: 768px) {
  h2--number,
.heading-2--number {
    font-size: var(--fs-800);
    margin-bottom: 0.5rem;
  }
}
@media (min-width: 768px) {
  h2,
.heading-2 {
    font-size: var(--fs-500);
  }
}

h3,
.heading-3 {
  font-size: var(--fs-300);
  margin-bottom: 1rem;
}
h3--upper,
.heading-3--upper {
  text-transform: uppercase;
}
@media (min-width: 768px) {
  h3,
.heading-3 {
    font-size: var(--fs-400);
  }
}

p,
.p {
  font-weight: 600;
  line-height: 1.6;
  font-family: "Open Sans", sans-serif;
  font-size: var(--fs-200);
  color: var(--secondary-very-dark-cyan);
  margin-bottom: 0.5rem;
}
p--light,
.p--light {
  color: var(--secondary-light-gray);
}
p--white,
.p--white {
  color: var(--secondary-white);
}
@media (min-width: 768px) {
  p,
.p {
    font-size: var(--fs-300);
    line-height: 1.4;
  }
}

.btn {
  background-color: transparent;
  border: 1px solid var(--primary-pink);
  border-radius: 2rem;
  text-transform: capitalize;
}
.btn--primary {
  background-color: var(--primary-pink);
  color: var(--secondary-very-pale-blue);
  padding: 1rem 3rem;
  font-weight: 700;
  box-shadow: 0 2px 5px 3px var(--secondary-shadow-gray);
  transition: ease 0.3s all;
}
.btn--primary:hover {
  background-color: var(--primary-light-pink);
  border-color: var(--primary-light-pink);
  cursor: pointer;
}
@media (min-width: 768px) {
  .btn--primary {
    padding: 1.5rem 7rem;
    font-size: var(--fs-300);
    border-radius: 4rem;
  }
}
.btn--secondary {
  padding: 0.4rem 1.5rem;
  color: var(--primary-pink);
  font-size: var(--fs-100);
  transition: ease 0.3s all;
}
.btn--secondary:hover {
  color: var(--primary-light-pink);
  border-color: var(--primary-light-pink);
  cursor: pointer;
}
@media (min-width: 768px) {
  .btn--secondary {
    font-size: var(--fs-200);
    padding: 0.6rem 1.5rem;
  }
}
.btn--tertiary {
  padding: 0.8rem 2.5rem;
  border-radius: 5px;
  background-color: var(--primary-pink);
  color: var(--secondary-white);
  font-weight: bold;
  transition: ease 0.3s all;
}
.btn--tertiary:hover {
  background-color: var(--primary-light-pink);
  border-color: var(--primary-light-pink);
  cursor: pointer;
}

.cta {
  text-align: center;
  margin: 0 auto;
  padding: 6rem 2rem 6rem;
  max-width: 65rem;
  background-color: var(--secondary-white);
}
@media (min-width: 768px) {
  .cta {
    padding: 8rem 2rem 6rem;
  }
}
.cta__content {
  margin-bottom: 6rem;
}
@media (min-width: 768px) {
  .cta__content {
    margin-bottom: 8rem;
  }
}
.cta__content__info {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .cta__content__info {
    max-width: 40rem;
    margin: 0 auto 3rem;
  }
}
.cta--simple {
  padding: 6rem 2rem 12rem;
}
.cta--simple__content {
  margin: 0;
}
.cta--simple__content__heading {
  margin-bottom: 3rem;
}

.numbers {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  max-width: 65rem;
  width: 100%;
  margin-bottom: 4rem;
  background-color: var(--secondary-white);
}
@media (min-width: 768px) {
  .numbers {
    flex-direction: row;
    justify-content: space-around;
  }
}
.numbers__card {
  text-align: center;
}
.numbers__card img {
  width: 40px;
}
@media (min-width: 768px) {
  .numbers__card img {
    width: 50px;
  }
}

.infoblock {
  padding: 6rem 2rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .infoblock--swapped {
    flex-direction: row !important;
  }
}
.infoblock__content {
  text-align: center;
}
@media (min-width: 768px) {
  .infoblock__content {
    width: 50%;
    text-align: left;
  }
}
@media (min-width: 768px) {
  .infoblock img {
    width: 50%;
  }
}
@media (min-width: 1440px) {
  .infoblock img {
    padding: 4rem;
  }
}
@media (min-width: 768px) {
  .infoblock {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    gap: 6rem;
    padding: 2rem 6rem;
  }
}
.infoblock--blue {
  background-color: var(--secondary-very-pale-blue);
}
.infoblock__image {
  margin-bottom: 4rem;
}

.bgcurve {
  background-color: var(--secondary-white);
}
.bgcurve img {
  vertical-align: top;
}
.bgcurve .desktop-version {
  display: none;
}
@media (min-width: 768px) {
  .bgcurve .desktop-version {
    display: initial;
  }
}
.bgcurve .mobile-version {
  display: initial;
}
@media (min-width: 768px) {
  .bgcurve .mobile-version {
    display: none;
  }
}

footer,
.footer {
  background-color: var(--secondary-very-dark-cyan);
  padding: 3rem 2rem;
  color: var(--secondary-white);
}
@media (min-width: 1440px) {
  footer,
.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 6rem 6rem;
    gap: 20%;
  }
}
footer__newsletter,
.footer__newsletter {
  margin-bottom: 6rem;
}
footer__newsletter__form,
.footer__newsletter__form {
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: center;
}
footer__newsletter__form input,
.footer__newsletter__form input {
  width: 100%;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  height: 50px;
  border-radius: 5px;
  outline: none;
  border: none;
}
footer__info__intro,
.footer__info__intro {
  margin-bottom: 3rem;
}
footer__info__intro img,
.footer__info__intro img {
  width: 220px;
  margin-bottom: 1rem;
}
footer__info__contact,
.footer__info__contact {
  margin-bottom: 3rem;
}
footer__info__contact div,
.footer__info__contact div {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 1.5rem;
  margin-bottom: 0.5rem;
}
footer__info__contact div img,
.footer__info__contact div img {
  width: 25px;
}
footer__info__socials,
.footer__info__socials {
  display: flex;
  align-items: center;
  gap: 1rem;
}
footer__info__socials img,
.footer__info__socials img {
  width: 30px;
  fill: white;
}

header,
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  background-color: var(--secondary-white);
}
@media (min-width: 768px) {
  header,
.header {
    padding: 4rem;
  }
}
header__logo-container,
.header__logo-container {
  width: 120px;
}
@media (min-width: 768px) {
  header__logo-container,
.header__logo-container {
    width: 220px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 3rem;
  max-width: 62.5rem;
}