*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 100%;
}

ul,
ol {
    list-style: none;
}

body {
    min-height: 100vh;
    font-family: "Open Sans", sans-serif;
    background-color: var(--secondary-white);
}

img {
    width: 100%;
}
